// Styles
import '@styles/iframe_block_page.scss';

// Images
import '@images/iframe/block.png';

// Common
import { extension } from '@extension/common/webextension';
import { ContentCommands } from '@extension/common/commands';

extension.runtime.onMessage.addListener((request, sender) =>
{
  if (sender.id !== extension.runtime.id) return;

  if (request.command === ContentCommands.UNBLOCK_IFRAME)
  {
    window.location.replace(request.url);
  }
});
